<style scoped>

.master-content {
    background: #fff;
    height: 100%;
    padding: 10px;
}

.master-content .row {
    padding: 10px;
    display: -webkit-flex;
    /* Safari */
    display: flex;
    align-items: center;
}

.master-content .row label {
    width: 100px;
}

.master-content .row.down {
    border-top: 1px solid #eaeaea;
    height: 100px;
}

.label-right-active {
    color: red !important;
}

.label-right {
    display: inline-block;
    margin-left: 20px;
}

</style>

<template>

<div>
    <Modal v-model="modal2" title="修改订单" :styles="{width:'100%'}" class-name="modal-box">

        <div class="master-content">
            <div class="row">
                <label>订单名称:</label>
                <Input v-model="orderName" type="text" name="orderName" placeholder="" @on-blur='userDataNum' @on-focus='onFocusName' style="width: 300px"></Input>
                <span :class="{'label-right-active':isAtive}" class="label-right">{{soltNameLen}}</span>
            </div>
            <div class="row">
                <label>广告客户:</label>
                <div class="main-element">
                    <el-select style="width: 300px;" v-model="customId" filterable placeholder="请选择">
                        <el-option v-for="item in customList" :key="item.fxAdCustomId" :label="item.customName" :value="item.fxAdCustomId">
                        </el-option>
                    </el-select>
                </div>
                <template v-if="false">
                  <Select v-model="customId" filterable style="width: 300px">
                      <Option v-for="item in customList" :label="item.customName" :value="item.fxAdCustomId" :key="item.fxAdCustomId">{{ item.customName}}
                      </Option>
                  </Select>
                </template>
            </div>

            <div class="row">
                <label>合同号:</label>
                <Input v-model="contractNo" type="text" name="contractNo" placeholder="" @on-blur='contracDataNum' @on-focus='onFocuscontrac' style="width: 300px"></Input>
                <span :class="{'label-right-active':isAtiveContractNo}" class="label-right">{{contractNLen}}</span>
            </div>


        </div>


        <div slot="footer">
            <Button type="text" @click="cancel">取消</Button>
            <Button class="btnColor" type="primary" @click="updateOrder">保存并启用</Button>
        </div>
    </Modal>
</div>

</template>

<script>

export default {
    data() {
            return {

                orderName: "",
                contractNo: "",
                customId: "",
                isAtive: false,
                isAtiveContractNo: false,
                soltNameLen: '最多可输入50个字符',
                contractNLen: '最多可输入120个字符',
                customList: [],
                orderId: "",
                modal2: false,
            }


        },
        created() {
            this.getCustomData();
            // this.getOrderData();
        },

        methods: {
            beforeLoad: function() {

                this.orderName = "";
                this.contractNo = ""
                this.orderId = "";
                this.soltNameLen = "最多可输入50个字符";
                this.isAtive = false;
                this.contractNLen = "最多可输入120个字符";
                this.isAtiveContractNo = false;
                this.modal2 = true;
            },
            cancel() {
                this.soltNameLen = "最多可输入50个字符";
                this.isAtive = false;
                this.contractNLen = "最多可输入120个字符";
                this.isAtiveContractNo = false;
                this.modal2 = false;
            },
            getCustomData: function() {
                let url = "/custom/getAllCustomList.json?status=" + 1;
                this.$Http.get(url, {}).then(res => {

                    if (res.body.status == 0) {
                        let r = res.body.data.customList;
                        console.log(r);
                        this.customList = r;
                        console.log(this.customList);
                    }
                });
            },
            getOrderData: function(orderId) {
                // this.orderId=this.$route.query.orderId;
                this.orderId = orderId;
                let url = "/order/getOrderById.json?orderId=" + this.orderId;
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status == 0) {
                        let r = res.body.data;
                        console.log(r);
                        this.orderName = r["orderName"];
                        this.contractNo = r["contractNo"];
                        this.customId = r["fxadCustomId"];
                        this.orderId = r["fxAdOrderId"];
                        this.modal2 = true;
                    }
                });
            },
            checkUpOrder() {
                if (this.orderName === "") {
                    this.$Message.error("订单名称不能为空");
                    return false;
                }
                if (this.customId === "") {
                    this.$Message.error("广告客户不能为空");
                    return false;
                }
                if (this.contractNo === "") {
                    this.$Message.error("合同号不能为空");
                    return false;
                }
                return true;
            },
            ajaxUpOrder(){
              this.userDataNum();
              this.contracDataNum();

              if (this.isAtiveContractNo || this.isAtive) {
                  return false;
              }

              let url = "/order/updateOrder.json?orderName=" + encodeURIComponent(this.orderName) + "&customId=" + this.customId + "&contractNo=" + encodeURIComponent(this.contractNo) + "&orderId=" + this.orderId;
              this.$Http.get(url, {}).then(res => {
                  if (res.body.status == 0) {
                      // this.$router.push("/orderManagement");
                      this.modal2 = false;
                      this.$emit("filterParent");
                  } else {
                      this.modal2 = false;

                  }
              });

            },
            updateOrder: function() {
              if(this.checkUpOrder()) {
                this.ajaxUpOrder();
              }
            },

            loadMore: function() {
                this.pageNo = this.pageNo + 1;
                this.getCustomData();
            },
            onFocusName() {
                this.soltNameLen = "最多可输入50个字符";
                this.isAtive = false;

            },
            userDataNum() {
                //姓名位数不在0-100间
                if (this.orderName) {
                    var reg = /^[\s\S]{1,50}$/;
                    var r = this.orderName.match(reg);
                    if (r == null) {
                        this.soltNameLen = "长度超过50个字符"
                        this.isAtive = true;
                    }
                } else {
                    this.soltNameLen = "订单名称不能为空"
                    this.isAtive = true;
                }
            },
            onFocuscontrac() {
                this.contractNLen = "最多可输入120个字符";
                this.isAtiveContractNo = false;

            },
            contracDataNum() {
                //姓名位数不在0-100间
                if (this.contractNo) {
                    var reg = /^[\s\S]{1,120}$/;
                    var r = this.contractNo.match(reg);
                    if (r == null) {
                        this.contractNLen = "长度超过120个字符"
                        this.isAtiveContractNo = true;
                    }
                } else {
                    this.contractNLen = "合同号不能为空"
                    this.isAtiveContractNo = true;
                }
            },


        }

}

</script>



